
.garden-item-container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 2px solid var(--green);
    width: 350px;
    height: 225px;
    color: white;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 12px;
    opacity: 0.6
}

.garden-item-container::after {
    content: "";
    background-image: url('../../../images/soil.jpeg');
    background-size: 95%;
    border-radius: 10px;
    opacity: 0.9;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -2;  
}
/* 
.garden-item-container:hover {
    background-color: rgb(0, 65, 0);
} */

.garden-item-container:hover {
    opacity: 0.85;
    cursor: pointer;
}

.garden-item-btn-container {
    position: absolute;
    font-size: 1.25rem;
    right: 10px;
    top: 2px;
}

.garden-item-btn-container:hover {
    color: var(--btn-color);
}

.garden-item-more-details {
    transition: letter-spacing 1s;
}
.garden-item-more-details:hover {
    font-weight: 500;
    letter-spacing: 1px;
}


.garden-item-text-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-size: cover;
    width: 85%;
    height: 175px;
    border-radius: 12px;
    background-color: rgba(98, 172, 98, 0.6);
}



