@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cabin:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster+Two:ital,wght@0,400;0,700;1,400;1,700&display=swap);
:root {
  --body-font: 'Cabin', sans-serif;
  --subheading-font: 'Lobster Two', cursive;
  --heading-font: 'Lobster', cursive;
  --btn-color: forestgreen;
  --brown: #6F4E37;
  --green: #2E8B57;
}

.flagship-btn {
  background-color: #6F4E37;
  background-color: var(--brown);
  font-family: 'Cabin', sans-serif;
  font-family: var(--body-font);
  font-weight: 700;
  min-width: 175px;
  height: 65px;
  color: white;
  border: 1px solid 	beige;
}

.flagship-btn:hover {
  background-color: white;
  color: #6F4E37;
  color: var(--brown);
  border: 1px solid #6F4E37;
  border: 1px solid var(--brown)
}





body {
background-color:#ececec;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header-container {
    position: relative;
    background-color: #ffffff;
    border-bottom: 1px solid var(--btn-color);
    height: 15vh;
}

.logo-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 15vh;
    position: relative;
    left: 20px;

    cursor: pointer;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: auto;
}

.header-title {
    font-family: 'Lobster', cursive;
    font-size: 4rem;
    letter-spacing: 0.15rem;
    position: relative;
    top: 5px;
    color: var(--brown);

}

.header-leaf {
    height: 65px;
}

.header-user-container {
    display: flex;
    position: absolute;
    right: 40px;
    top: 20px;
}

.header-user-container:hover {
    color: darkgreen;
    cursor:pointer;
}

.header-username {
    margin-right: 0.5rem;
    position: relative;
    top: 5px;
    font-weight: 500;
}


.header-user-icon {
    width: 32.25px;
    position: relative;
    bottom: 0.8px;
    right: 1.5px;
}

.header-user-icon-container {
    height: 35px;
    width: 35px;
    border: 3px solid green;
    border-radius: 100%;
    overflow: hidden;
}

.header-user-collapse {
    display: flex;
    flex-direction: column;
    position: absolute;
    right: 40px;
    top:60px;
    background-color: white;
    border: 1px solid green;
    padding: 0.5rem;
    border-radius: 5px;
}
    
.header-user-profile-btn, .header-user-logout-btn {
    border: none;
    font-weight: 500;
    background-color: white;
    margin: 0.25rem;
    }


    .header-user-profile-btn:hover, .header-user-logout-btn:hover {
        text-decoration: underline;
        color: green;
    }

