.splash-main-heading {
    margin-top: 5rem;
    font-size: 3.25rem;
    padding-bottom: 0.25rem;
}

.splash-sub-heading {
    font-size: 1.5rem;
    padding-bottom: 1rem;
}

.flourish-span {
    font-family: "lobster", cursive;
    color: #1c917d;
    letter-spacing: 1px;
    font-size: 3.5rem;
}

.splash-description {
    font-size:0.95rem;
    line-height: 2rem;
}

.splash-list {
    list-style: none;
    text-align: left;
    margin: auto;
}

.splash-lobster-font {
    font-family: var(--subheading-font);
    font-size: 1.15rem;
}



.splash-soil {
    color:#26aa95;
    font-weight: 900;
}
.splash-vegetables {
    color:#229986;
    font-weight: 900;
}
.splash-companion {
    color:#1E8877;
    font-weight: 900;
}
.splash-layout {
    color:#1A7768;
    font-weight: 900;
}
.splash-summary {
    color:#166659;
    font-weight: 900;
}

.splash-list-step {
    font-size: 0.85rem;
}


.splash-btn-group {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 400px;
    margin: auto;
    padding-top: 2rem;

}





.garden-item-container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 2px solid var(--green);
    width: 350px;
    height: 225px;
    color: white;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 12px;
    opacity: 0.6
}

.garden-item-container::after {
    content: "";
    background-image: url(/static/media/soil.f7fe17cc.jpeg);
    background-size: 95%;
    border-radius: 10px;
    opacity: 0.9;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -2;  
}
/* 
.garden-item-container:hover {
    background-color: rgb(0, 65, 0);
} */

.garden-item-container:hover {
    opacity: 0.85;
    cursor: pointer;
}

.garden-item-btn-container {
    position: absolute;
    font-size: 1.25rem;
    right: 10px;
    top: 2px;
}

.garden-item-btn-container:hover {
    color: var(--btn-color);
}

.garden-item-more-details {
    transition: letter-spacing 1s;
}
.garden-item-more-details:hover {
    font-weight: 500;
    letter-spacing: 1px;
}


.garden-item-text-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-size: cover;
    width: 85%;
    height: 175px;
    border-radius: 12px;
    background-color: rgba(98, 172, 98, 0.6);
}



