.garden-list-grid-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    justify-content: center;
    justify-items: center;
    align-content: center;
    align-items: center;
    grid-gap: 25px;
    gap: 25px;
    margin: 0rem auto;
    max-width: 1200px;
}

.garden-list-add-btn {
    margin-bottom: 2rem;
}

.garden-list-past-gardens {
    margin-top: 4rem;
}

.create-garden-btn {
    margin-top: 5rem;
}

.garden-detail-container {
