.summary-report-main-heading, .summary-report-date {
    text-align: center;
}

.summary-report-container {
    max-width: 950px;
    background-color: white;
    margin: 3rem auto;
    padding: 2rem;
    border-radius: 1rem;
    text-align: left;

}

.modal-content {
    overflow:visible;
}

.modal {
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    padding: 0;
    overflow: visible!important;
}


.modal-body {
    width: auto;
    height: 100%;
    overflow:visible !important;
    padding: 0;
}

.summary-vegetable {
    height: auto;
    overflow: visible;
}

.summary-table {
    text-align: left;
}

.summary-td {
    width: 50px;
    min-width: 100px;
}

.summary-companions {
    max-width: 75px;
    width: 75px;
}

.summary-category {
    font-weight: 500;
    color: var(--green)
}

div[role="dialog"] {
    padding: 0 !important;
}

.summary-print-vegetables {
    position: absolute;
    right: 60px;
}

.modal-fullscreen {
    min-height: 100vh;
}

#scroll {
    overflow: scroll;
    margin: 2rem auto;
    max-width: 80vw;
}


.navbar-container {
    margin: auto;
    left: 0;
    right: 0;
    background-color: white;
    height: 10vh;
}

.navbar-radio-btn {
    width: 200px;
    color: var(--btn-color);
    font-weight: 600;
    border: none;
}

.nav-btn:hover {
    background-color: var(--btn-color);
}

.nav-btn:active {
    background-color: var(--btn-color);
}
.btn-check:checked+.btn-outline-success {
    background-color: var(--btn-color);
    border-color: var(--btn-color);
}

.nav-btn-group {
    border: 1px solid var(--btn-color);
    width: 800px;
    margin: auto;
    border-radius: 0.5rem;
}

.footer-logo-link {
    text-decoration: none;
    color: darkgreen;
}

.footer-container {
    font-family: var(--body-font);
    letter-spacing: 0.05rem;
}
.varieties-detail-container{
    width: 300px;
    /* margin-bottom: 2rem; */
    border: 1px solid white;
    position: relative;
}

.varieties-detail-name{
    font-family: var(--subheading-font);
    font-size: 1.5rem;
    text-align: center;
}

.varieties-detail-input{
    position:relative;

}
